export const routes = [
    {
        path:'/',
        redirect:'/music'
    },
    {
        path:'/login',
        component:() => import('@/views/login'),
        meta:{
            title:'登录'
        }
    },
    {
        path:'/registry',
        component:() => import('@/views/registry'),
        meta:{
            title:'注册'
        }
    },
    {
        path:'/music',
        component:() => import('@/views/music'),
        meta:{
            title:'音乐'
        }
    },
    {
        path:'*',
        component:() => import('@/views/404')
    }
]





